'use strict';
/**
 * @ngdoc overview
 * @name mytodoApp
 * @description
 * # mytodoApp
 *
 * Main module of the application.
 */
angular.module('mytodoApp', [
  'ngAnimate',
  'ngCookies',
  'ngResource',
  'ngRoute',
  'ngSanitize',
  'ngTouch',
  'ui.sortable',
  'mgcrea.ngStrap'
]).config([
  '$routeProvider',
  function ($routeProvider) {
    $routeProvider.when('/', {
      templateUrl: 'views/main.html',
      controller: 'MainCtrl'
    }).when('/about', {
      templateUrl: 'views/about.html',
      controller: 'AboutCtrl'
    }).when('/login', {
      templateUrl: 'views/login.html',
      controller: 'LoginCtrl'
    }).otherwise({ redirectTo: '/' });
  }
]);
'use strict';
/**
 * @ngdoc function
 * @name mytodoApp.controller:MainCtrl
 * @description
 * # MainCtrl
 * Controller of the mytodoApp
 */
angular.module('mytodoApp').controller('MainCtrl', [
  '$scope',
  function ($scope) {
    $scope.todos = [
      'Item 1',
      'Item 2',
      'Item 3'
    ];
  }
]);
'use strict';
/**
 * @ngdoc function
 * @name mytodoApp.controller:AboutCtrl
 * @description
 * # AboutCtrl
 * Controller of the mytodoApp
 */
angular.module('mytodoApp').controller('AboutCtrl', [
  '$scope',
  function ($scope) {
    $scope.awesomeThings = [
      'HTML5 Boilerplate',
      'AngularJS',
      'Karma'
    ];
  }
]);
/* global angular */
'use strict';
/**
 * @ngdoc function
 * @name mytodoApp.controller:LoginCtrl
 * @description
 * # LoginCtrl
 * Controller of the mytodoApp
 */
angular.module('mytodoApp').controller('LoginCtrl', [
  '$scope',
  '$location',
  '$rootScope',
  '$http',
  '$cookies',
  function ($scope, $location, $rootScope, $http, $cookies) {
    $scope.credentials = {};
    $scope.login = function () {
      var credentials = $scope.credentials;
      var headers = credentials ? { authorization: 'Basic ' + btoa(credentials.username + ':' + credentials.password) } : {};
      $http.get('http://localhost:8080/session-user', { headers: headers }).success(function (data) {
        if (data) {
          console.log('Login succeeded');
          $location.path('/');
          $scope.error = false;
          $rootScope.authenticated = true;
        } else {
          console.log('Login failed');
          $scope.error = true;
          $rootScope.authenticated = false;
        }
      }).error(function () {
        console.log('Login failed');
        $scope.error = true;
        $rootScope.authenticated = false;
      });
    };
  }
]);
'use strict';
/**
 * @ngdoc function
 * @name mytodoApp.controller:NavigationCtrl
 * @description
 * # NavigationCtrl
 * Controller of the mytodoApp
 */
angular.module('mytodoApp').controller('NavigationCtrl', [
  '$scope',
  '$rootScope',
  '$http',
  '$location',
  '$element',
  function ($scope, $rootScope, $http, $location, $element) {
    $http.get('http://localhost:8080/session-user').success(function (data, status, headers, config) {
      if (data !== '') {
        $rootScope.sessionUser = data;
      }
      $rootScope.csrfToken = headers('x-csrf-token');
    });
  }
]);